.main__bg{
    height: 100vh;
    width: 100vw;
    position: relative;
    z-index: 20;
}

.main__bg video{
    width: 100%;
    height: 100%;
    z-index: 15;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover;
    pointer-events: none;
}